// BASIC SETUP

* {
    box-sizing: border-box;
}

body,
html {
    padding: 0;
    margin: 0;
    font-family: "Raleway", sans-serif;
    color: #404040;
    scroll-behavior: smooth;
}

button {
    font-family: "Raleway", sans-serif;
    cursor: pointer;
}

// HOME

.home__container {
    background-color: #e6d3a3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;

    &__header {
        font-size: 600%;
        margin-bottom: 0;

        span {
            color: #91972a;
        }

        @media (max-width: 800px) {
            font-size: 450%;
            padding: 4rem;
        }

        @media (max-width: 550px) {
            margin-right: 6rem;
        }

        @media (max-width: 360px) {
            font-size: 350%;
            padding-left: 3rem;
        }
    }

    &__slogan {
        font-size: 160%;
        color: #404040;

        span {
            color: #91972a;
        }
        @media (max-width: 800px) {
            font-size: 140%;
        }

        @media (max-width: 500px) {
            font-size: 130%;
            width: 85%;
        }
    }

    &__flag {
        width: 30px;
        margin-left: 0.5em;
    }
}

// NAV

.main_nav {
    position: fixed;
    top: 1.5rem;
    right: 0;
    z-index: 2;

    a {
        font-size: 120%;
        text-decoration: none;
        padding: 2rem;
        color: #91972a;

        @media (max-width: 560px) {
            font-size: 90%;
            padding: 0.75rem;
            border-radius: 1.2rem;
        }
    }

    a:hover {
        color: #404040;
        transition: color 0.2s;
    }
}

header {
    --text: #f4f4f4;
    --text-inverse: #f4f4f4;
    --background: transparent;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2em 3em;
    transition: background 250ms ease-in;
    background: var(--background);
    color: var(--text);

    &:hover {
        background: var(--text);
        color: var(--text-inverse);
        border-color: var(--text);
    }
}

.nav-scrolled {
    --text: #333;
    --text-inverse: #f4f4f4;
    --background: #f4f4f4;
    box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);

    a:hover {
        color: #f4f4f4;
        transition: color 0.2s;
    }
}

// SEARCH BAR

.filter {
    margin-top: 2em;
    padding: 0.5em;
    display: flex;
    justify-content: center;
    width: 30em;

    @media (max-width: 600px) {
        width: 25em;
    }

    @media (max-width: 410px) {
        width: 20em;
    }

    @media (max-width: 330px) {
        width: 15em;
    }
}

.filter__input {
    padding: 1em;
    outline: none;
    border-style: none;
    opacity: 0.5;
    width: 200%;
    font-size: 1.3em;
}

.list {
    display: flex;
    flex-direction: column;
    color: #404040;
    padding: 1em;
    border-bottom: 1px solid #91972a;
}

.country-select {
    font-size: 1.75rem;
    font-weight: bold;

    @media (max-width: 700px) {
        font-size: 1rem;
    }
}

.country-select:hover {
    cursor: pointer;
    color: #91972a;
}

// HERO

.hero__container {
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
        url("../img/kitchen.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #e6d3a3;

    blockquote {
        font-size: 200%;
        font-style: italic;
        padding: 1rem;
        border-radius: 0.75rem;

        @media (max-width: 880px) {
            font-size: 120%;
        }
        @media (max-width: 500px) {
            font-size: 80%;
        }
        @media (max-width: 360px) {
            padding: 0.5rem;
        }
    }

    cite {
        display: inline-block;
        margin-left: 4rem;

        @media (max-width: 360px) {
            font-size: 80%;
            padding: 0.5rem;
        }
    }
}

// COUNTRY RESULTS

.country {
    background-color: #f2f4f3;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__heading {
        color: #91972a;
        font-size: 300%;
        margin-top: 5rem;

        @media (max-width: 800px) {
            font-size: 250%;
        }

        @media (max-width: 720px) {
            font-size: 250%;
        }

        @media (max-width: 530px) {
            font-size: 200%;
            align-items: center;
        }
        @media (max-width: 570px) {
            font-size: 180%;
        }

        @media (max-width: 400px) {
            font-size: 150%;
        }

        @media (max-width: 330px) {
            font-size: 120%;
        }
    }

    &__tag {
        font-size: 120%;
        @media (max-width: 570px) {
            font-size: 100%;
        }

        @media (max-width: 330px) {
            font-size: 80%;
        }
    }

    &__options {
        color: #91972a;
        text-align: center;
        margin-top: 4rem;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;

        @media (max-width: 800px) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__option {
        transition: 0.3s;
        overflow: hidden;
        margin-bottom: 4rem;
        font-size: 150%;
        position: relative;

        img {
            width: 230px;
            height: 180px;
            // height: auto;
            object-fit: cover;

            opacity: 0.9;
            transform: scale(1.15);
            transition: transform 0.5s, opacity 0.5s;
        }
        img:hover {
            opacity: 1;
            transform: scale(1.03);

            cursor: pointer;
        }
    }

    &__name {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.322);
        color: #f2f4f3;
        visibility: hidden;
        opacity: 0;
    }

    .country__option:hover .country__name {
        visibility: visible;
        opacity: 1;
    }
}

// NO RESULTS

.no_results {
    background-color: #919720;
    color: #e3f0ff;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h2 {
        @media (max-width: 800px) {
            font-size: 100%;
        }
        @media (max-width: 570px) {
            font-size: 80%;
        }
        @media (max-width: 460px) {
            text-align: center;
        }
    }

    p {
        text-align: center;
    }

    a {
        text-decoration: none;
        color: #404040;
    }
}

// RECIPE RESULTS

.recipe {
    h3 {
        color: #919720;
    }

    &__container {
        background-color: rgba(190, 190, 190, 0.322);

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__card {
        background-color: #f2f4f3;
        margin: 5em 0;
        padding: 1em 1.5em;
        box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);

        @media (max-width: 480px) {
            padding: 2px 16px;
        }

        @media (max-width: 430px) {
            width: 100%;
        }

        @media (max-width: 320px) {
            padding: 2px 16px;
        }
    }

    &__easy {
        text-align: center;

        @media (max-width: 700px) {
            font-size: 90%;
        }
    }

    &__heading {
        font-size: 270%;
        margin-top: 3rem;
        text-align: center;

        @media (max-width: 700px) {
            font-size: 150%;
        }
    }

    &__requirements {
        display: flex;
        flex-direction: row;
        justify-content: center;
        font-size: 90%;

        @media (max-width: 700px) {
            display: block;
            padding: 0;
        }
    }

    &__is {
        display: flex;
        flex-direction: column;
        text-align: center;
        margin: 2.5rem 2.5rem;
    }

    &__dietary {
        color: #919720;
    }

    &__ingredients {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 2.5rem;

        @media (max-width: 1000px) {
            font-size: 80%;
        }

        @media (max-width: 940px) {
            font-size: 60%;
        }

        @media (max-width: 900px) {
            flex-direction: column;
            align-items: center;
            font-size: 100%;
        }
    }

    &__ingredient {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        line-height: 0.1;
        padding: 1.5rem;

        img {
            width: 500px;
            &:hover {
                transform: scale(1.03);
                transition: transform 0.5s, opacity 0.5s;
            }

            @media (max-width: 840px) {
                width: 350px;
            }

            @media (max-width: 550px) {
                width: 370px;
            }

            @media (max-width: 450px) {
                width: 200px;
            }
        }

        ul {
            list-style-type: circle;
        }

        @media (max-width: 840px) {
            font-size: 80%;
        }
        @media (max-width: 716px) {
            font-size: 65%;
        }

        @media (max-width: 700px) {
            font-size: 90%;
        }

        @media (max-width: 350px) {
            font-size: 65%;
        }
    }

    &__container__bottom {
        background-color: #e6d3a3;
        padding: 3.5em 6.5em;
        margin: 5em 0;

        display: flex;
        flex-direction: row;
        justify-content: center;

        @media (max-width: 800px) {
            font-size: 70%;
        }

        @media (max-width: 630px) {
            font-size: 60%;
        }
    }

    &__instructions {
        line-height: 2;
        width: 68%;

        padding: 3.5em 4.5em;
        box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);
        background-color: #f2f4f3;

        @media (max-width: 1000px) {
            width: 100%;
        }

        @media (max-width: 630px) {
            padding: 3.5em 3em;
        }

        @media (max-width: 400px) {
            width: 120%;
            padding: 3.5em 2em;
        }
    }

    &__review {
        display: flex;
        justify-content: center;
        text-decoration: none;
        color: #404040;

        span {
            font-style: italic;
            text-decoration: underline;
            color: #919720;
            &:hover {
                color: #404040;
            }
        }
    }

    a {
        font-style: italic;
        color: #919720;
        display: flex;
        justify-content: center;
    }

    &__container__bon {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        text-align: center;
        margin: 2.5em 0;

        img {
            width: 60px;
            margin-bottom: 4em;
        }
    }
}

// Footer

.footer {
    background-color: #404040;
    padding: 1em;
    font-size: 40%;

    @media (min-width: 700px) {
        font-size: 70%;
    }

    &__container {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }

    &__nav {
        list-style: none;
        display: flex;
        flex-direction: column;

        @media (max-width: 760px) {
            font-size: 70%;
        }
    }

    &__nav li,
    &__nav__social li {
        display: inline-block;
        padding: 0.5em;
    }

    &__nav li {
        color: #d8d174;

        &:hover {
            color: #919720;
        }
    }

    &__nav li a:link,
    &__nav li a:visited {
        text-decoration: none;
        border: 0;
        color: #d8d174;
        transition: color 0.2s;
    }

    &__nav li a:hover,
    &__nav li a:active {
        color: #919720;
    }

    &__copyright {
        color: #d8d174;
        text-align: center;
        margin-top: 1em;

        @media (max-width: 760px) {
            font-size: 70%;
        }

        @media (max-width: 500px) {
            font-size: 60%;
            margin-left: 10em;
        }
    }
}

.facebook,
.twitter,
.google,
.instagram {
    transition: color 0.2s;
    text-decoration: none;
    font-size: 18px;
    border: 0;
    color: #d8d174;

    @media (max-width: 760px) {
        font-size: 14px;
    }
}

.facebook:hover {
    color: #3b5998;
}

.twitter:hover {
    color: #00aced;
}

.google:hover {
    color: #dd4b39;
}

.instagram:hover {
    color: #517fa4;
}

// LOGIN

.login {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
        url("../img/hero.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;

    &__container {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        min-height: 100vh;
        padding: 20px;
    }

    &__form {
        border-radius: 2px 2px 5px 5px;
        padding: 10px 20px 20px 20px;
        width: 90%;
        max-width: 320px;
        background: #f2f4f3;
        position: relative;
        padding-bottom: 80px;
        box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
    }

    &__title {
        color: #404040;
        font-size: 1.2em;
        font-weight: bold;
        margin: 10px 0 30px 0;
        border-bottom: 1px solid #eee;
        padding-bottom: 20px;
    }

    &__input {
        display: block;
        padding: 15px 10px;
        margin-bottom: 10px;
        width: 100%;
        border: 1px solid #ddd;
        transition: border-width 0.2s ease;
        border-radius: 2px;
        color: #404040;
    }

    &__register {
        font-size: 0.8em;
        color: #404040;

        a {
            color: #919720;
            text-decoration: none;
        }
    }

    &__btn {
        width: 100%;
        height: 100%;
        padding: 10px 10px;
        background: #919720;
        color: #f2f4f3;
        font-size: 120%;
        display: block;
        border: none;
        margin-top: 1em;
        position: absolute;
        left: 0;
        bottom: 0;
        max-height: 60px;
        border: 0px solid rgba(0, 0, 0, 0.1);
        border-radius: 0 0 2px 2px;
        border-bottom-width: 7px;
    }

    &__btn:hover {
        cursor: pointer;
    }
}

