// PROFILE

#profile-form {
    @media (max-width: 630px) {
        margin-left: 0;
    }
}

.profile {
    background-color: #e6d3a3;

    @media (max-width: 800px) {
        font-size: 90%;
    }

    @media (max-width: 705px) {
        font-size: 80%;
    }

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 5em;

        @media (max-width: 48px) {
            padding: 0;
            width: 100%;
        }
    }

    &__admin {
        color: red;
        opacity: 0.6;
    }

    &__user {
        // display: flex;
