    }

    &__add {
        background: #919720;
        margin-left: 0.5em;

        &:hover {
            background-color: #565a01;
        }
    }

    &__remove {
        margin-left: 1em;
        background-color: rgb(255, 177, 177);

        &:hover {
            background-color: rgb(255, 61, 61);
        }
    }

    &__btn {
        background: #919720;
        color: #f2f4f3;
        margin-top: 2em;
        padding: 8px 8px;
        border: none;
        border-radius: 0.75rem;
        font-size: 100%;

        &:hover {
            background-color: #565a01;
        }
    }
}

// ADMIN

.searchAdmin {
    &__btn {
        background: #919720;
        color: #f2f4f3;
        margin-top: 2em;
