        color: #919720;
        margin-top: 1em;
    }
}

// ABOUT

.about {
    background-color: #f2f4f3;

    &__container {
        height: 100vh;
        display: flex;
        justify-content: space-around;
        align-items: center;

        @media (max-width: 460px) {
            margin-top: 10em;
            background-color: #fff;
            flex-direction: column;
            height: auto;
        }
    }

    &__card {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        transition: 0.3s;
        width: 20%;
        background-color: #919720;
        opacity: 0.9;

        @media (max-width: 1160px) {
            font-size: 80%;
        }

        @media (max-width: 900px) {
            font-size: 70%;
            width: 25%;
        }

        @media (max-width: 460px) {
            width: 50%;
        }

        &:hover {
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
            opacity: 1;
        }

        img {
            width: 100%;
            height: 250px;
            object-fit: cover;
        }

        h4 {
            text-align: center;
            font-size: 130%;
            color: #f2f4f3;

            @media (max-width: 90px) {
                font-size: 90%;
            }
        }

        p {
            font-size: 85%;
            color: #f2f4f3;
            padding-bottom: 1em;
        }

        b {
            color: #404040;
        }
    }

    &__person {
        padding: 2px 16px;
    }
}

// REVIEWS

#reviews {
    width: 100%;
    height: auto;
}

.review {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
        url("../img/ingredients.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #f2f4f3;
    min-height: 100vh;

    &__container {
        display: flex;
        justify-content: center;
        align-items: center;

        @media (max-width: 850px) {
            flex-direction: column;
        }
    }

    &__form {
        margin: 3em;
    }

    &__image {
        color: #919720;

        h1 {
            @media (max-width: 1300px) {
                margin-top: 6em;
                font-size: 120%;
            }

            @media (max-width: 580px) {
                margin-top: 10em;
                font-size: 80%;
            }

            @media (max-width: 400px) {
                margin-left: 1em;
            }
        }

        &__heading {
            text-align: center;
            color: #919720;
        }
    }

    &__group {
        outline: none;
        border-style: none;
    }

    &__remove,
    &__edit,
    &__btn {
        padding: 0.6em;

        background-color: rgba(59.9, 59.2, 12.5, 0.7);
        color: #f2f4f3;

        &:hover {
            cursor: pointer;
            background-color: #919720;
            color: #f2f4f3;
        }
    }

    &__btn {
        background-color: #919720;
        margin-top: 1em;
        font-size: 100%;

        &:hover {
            cursor: pointer;
            background-color: rgba(59.9, 59.2, 12.5, 0.7);
            color: #f2f4f3;
        }
    }

    &__remove,
    &__edit {
        // margin-left: 35em;
        border: none;
        color: #919720;
        // position: absolute;
        // top: 1;
        // bottom: 1;

        font-size: 60%;
    }

    &__comment {
        position: relative;
        background: rgba(59.9, 59.2, 12.5, 0.7);
        color: #f2f4f3;
        padding: 10px 30px;
        margin: 10px 0;
        word-wrap: break-word;

        box-shadow: 2px 3px 6px rgba(0, 0, 0, 0.3);

        cite {
            position: absolute;
            right: 0;
            top: 0;
            font-size: 65%;
            color: #919720;
        }
    }
}
